		branch: 'katarungan-branch',
		instagram: 'sylvasteven',
		story:
			'I’m a dog lover so when I hear about Cafés nearby our house that was opening. I open up my schedule for their opening. Since I’m living in an apartment and our apartment rules that no pets should allow so I have been missing to have a dog. So this Café bright up my life, funny right but I love dogs. Mascota De Café has been my hang-out place after work or when I don’t have work. There a dog that I been playing with and I want to adopt it but how? The landlord will surely get mad at me. I look for another rental place around that allowed dog and I found one. Thank god the dog that I was playing with wasn’t adopted yet. Thanks to the staff that serves the dog for me.',
